.product_article {
	padding: rem(72) 0;

	h2 {
		margin-bottom: rem(72);
		max-width: rem(480);

		margin-left: auto;
		margin-right: auto;
	}

	&__items {
		padding-bottom: rem(56);
		margin-bottom: rem(56);

		.item {
			margin-bottom: rem(28);


			h3 {
				margin-bottom: rem(8);
				font-size: rem(24);
			}

			p {
				font-size: rem(15);
			}
		}
	}
}