#header_info {
	height: rem(340);
	background: url(../img/page_info/header_bg_info_x2.jpg) no-repeat;
	background-size: cover;

	>.container {
		position: relative;

		&::before {
			bottom: 0;
			left: rem(-60);
			background: url(../img/page_info/header_i_left_x2.png) top center;
			background-size: 100%;
			width: rem(126);
			height: rem(266);
		}

		&::after {
			bottom: rem(10);
			right: rem(-60);
			background: url(../img/page_info/header_i_right_x2.png) top center;
			background-size: 100%;
			width: rem(104);
			height: rem(197);
		}
	}

	h1 {
		font-size: rem(40);
		font-weight: normal;
	}

	.header__bottom {
		
	}
}