.catalog {
	padding: rem(20) 0 rem(64) 0;

	&__header {
		
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		max-width: 100%;
		
		&::before {
			position: absolute;
			content: "";
			width: 100%;
			left: 0;
			margin-top: rem(50);
			border-bottom: rem(1) solid #E5E8EC;
		}

		span {
			margin-bottom: 0;
			margin-right: rem(65);
		}

		&-links {
			flex-direction: row;

			border-bottom: 0;
			border-top: 0;

			margin-left: 0;
			margin-right: 0;

			margin-bottom: 0;

			.item {
				flex: 0 0 auto;
				max-width: unset;

				margin-right: rem(40);
				justify-content: flex-start;

				&:last-child {
					margin-right: 0;
					border-right: 0;
				}

				a {
					margin-left: 0;
					pointer-events: painted;

					border-bottom: rem(2) solid transparent;

					&:hover {
						border-color: var(--c-accent);
					}
				}

				a.active {
					border-color: var(--c-accent);
				}

				button {
					display: none;
				}
			}
		}
	}

	&__info {
		flex: 0 0 100%;
		max-width: 100%;
		justify-content: flex-end;
		margin-top: rem(20);
		margin-bottom: rem(40);

		h2 {
			display: none;
		}

		.count {
			span {
				font-size: rem(11);
				font-weight: 800;

				&:first-child {
					margin-right: rem(8);
				}

				&:last-child {
					color: var(--c-accent);
				}
			}
		}
	}
	#catalog_mod_1 {
		display: none;
	}
	&__left {
		position: relative;
		top: auto;
		left: auto;
		flex: 0 0 25%;
		max-width: 25%;
		height: auto;
		background-color: white;
		z-index: 0;
		overflow-y: hidden;

		display: block;


		&-header {

			display: none;
		}

		&-nav {
			padding: 0;

			ul {
				border-bottom: rem(1) solid #E5E8EC;

				display: flex;
				flex-direction: column;

				margin-bottom: rem(25);

				li {

					&:last-child {
						margin-bottom: rem(10);
					}

					a {
						display: inline-block;
						font-weight: 500;
						line-height: 117%;
						padding: rem(5) 0;
						border-bottom: rem(2) solid transparent;

						margin-bottom: rem(16);

						&:hover {
							color: #4E545A;
							border-color: var(--c-accent);
						}

						&:active {
							color: var(--c-dark);
							// border-color: var(--c-dark);
						}
					}
				}

				li.active {
					a {
						border-color: var(--c-dark);
						color: var(--c-dark);
					}
				}
			}
		}
	}

	&__left#catalog_mod_1.active {
		display: none;
	}

	&__left#catalog_mod_2.active {
		display: none;
	}

	&__right {
		flex: 0 0 75%;
		max-width: 75%;
		padding-left: rem(24);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.product_item {
			width: calc((100% - 1.2rem) / 2);
		
			
		}
	}
}

.product_item {

	padding: rem(16) rem(6);

	margin-bottom: rem(20);

	&__image {
		display: flex;
		justify-content: center;
		margin-bottom: rem(12);
	}

	&__text {

		h3 {
			margin-bottom: rem(6);
			font-size: rem(18);
			font-weight: normal;
		}

		p {
			font-size: rem(15);
		}
	}
}


.page_nav {

	ul {

		li {

			span,
			a {
				font-size: rem(14);
			}


			.arrow {

				i {
					font-size: rem(24);
				}
			}
		}

	}
}