#header_goods_about {

	.header__top {
		margin-left: 66px;
		margin-right: 66px;
	}

	.header__bottom {
		margin-top: rem(40);

		&-left {
			height: rem(743);
			background: url(../img/page_goods_about/header_bg_x2.jpg) no-repeat top center;
			background-size: cover;
			padding-bottom: rem(138);
			padding-top: rem(20);

			.swiper-overlay {
				width: rem(591);
				height: rem(591);
				background-color: #F7FAFF;

				.swiper-container {
					padding-bottom: rem(70);
					margin-top: rem(125);
					// height: rem(250);
				}
			}

			.swiper-nav {
				bottom: rem(22);
				margin-left: rem(-64);
			}
		}

		&-right {
			margin: rem(72) 0;

			>span {
				font-size: rem(13);
			}

			h3 {
				font-size: rem(44);
			}

			p {
				margin-bottom: rem(16);
			}

			.icons {
				margin-right: rem(20);

				i {
					font-size: rem(25);
				}
			}

			.count {
				font-size: rem(14);
			}

			a.button_primary {
				order: 7;
				margin-top: 0;
			}

			ul {
				order: 5;
				margin-top: rem(48);
				margin-bottom: rem(56);

				li {
					font-size: rem(15);
				}
			}

			a {
				order: 6;
				margin-bottom: rem(46);
				font-size: rem(15);
			}
		}
	}
}

