#header_store {
	height: rem(393);
	background: url(../img/page_store/header_bg_x2.jpg) no-repeat;
	background-size: cover;

	>.container {
		position: relative;

		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: rem(54);
			width: rem(650);
			height: rem(159);
			background: url(../img/page_store/header_i_x2.png) no-repeat top center;
		}
	}

	.header__bottom {
		// position: relative;
		margin-top: rem(75);

		h1 {
			font-size: rem(40);
			font-weight: normal;
			margin-bottom: rem(20);
		}

	}
}