.slider_product {

	> .container {
		width: calc(100% - 30px);
	}

	.swiper-container {
		padding: rem(36) rem(60) rem(20) rem(60);
	}

	.swiper-nav {

		button {
			margin-top: rem(-56);
			margin-bottom: rem(80);
		}

		.swiper-button-disabled {
			i {
				font-size: rem(14);

				&::before {
					font-size: rem(14);
				}
			}
		}

		i {


			&::before {
				color: var(--c-accent);
				font-size: rem(14);
			}
		}

	}

	&__item {
		.image {
			width: rem(118);
			height: rem(118);

			img {
				top: 0;
			}
		}

		h3 {
			max-width: rem(160);
			font-size: rem(14);
		}
	}
}